.review {
  padding: 30px;
  background: #FFFFFF;
  border-radius: 8px;
  margin: 0 0 20px 0;
}
.review__title {
  margin: 0 0 20px 0;
}
.review__text {
  font-size: 15px;
  line-height: 30px;
  color: #111111;
  margin: 0 0 20px 0;
}
.review__list {
  display: flex;
  flex-direction: column;
}
.review__item {
  border-top: 1px solid #E7EAEE;
  font-size: 14px;
  line-height: 20px;
  color: #111111;
  display: grid;
  padding: 20px 0;
  grid-template-columns: 40% 60%;
  gap: 15px;
}

@media screen and (max-width: 1024px) {
  .review {
    padding: 30px 15px 20px;
